body {
  background: linear-gradient(to right, #050a25 30%, #464646);
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor {
  width: 100%;
  margin: 0 auto;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Raleway", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-weight: 500;
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.filtro {
  display: none;
}

.button {
  cursor: pointer;
  display: block;
  background-color: #050a25;
  border-radius: 7px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  width: 100%;
  margin-top: 1rem;
  max-width: 20rem;
  padding: 1.125rem;
  box-sizing: border-box;
  color: rgba(255, 255, 255, 0.5);
  font-size: 22px;
  align-self: center;
  text-align: center;
}

.button:hover {
  background-color: #050a25;
  border: 1px solid rgba(255, 255, 255, 0.8);
  color: #FFFFFF;
}

.boton-agregar {
  cursor: pointer;
  display: block;
  background-color: #050a25;
  border-radius: 7px;
  width: 200px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 1rem;
  box-sizing: border-box;
  color: rgba(255, 255, 255, 0.5);
  font-size: 19px;
  text-align: center;
}

.boton-agregar:hover {
  background-color: #050a25;
  border: 1px solid rgba(255, 255, 255, 0.8);
  color: #FFFFFF;
}

.simple-button {
  border: none;
  background-color: unset;
}

.simple-button--add {
  color: #2A7AE4;
}

.simple-button--delete {
  color: #ff1f1f;
}

.simple-button--edit {
  color: #1dc517;
}

.form__label {
  font-size: 19px;
  margin-bottom: 1rem;
}

.input-container {
  font-weight: 900;
  position: relative;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  box-sizing: border-box;
}

.input {
  box-sizing: border-box;
  border-bottom: 1px solid #2A7AE4;
  padding: 1.375rem 0.5rem 0.5rem;
  height: 3.25rem;
  width: 100%;
}

.input::placeholder {
  visibility: hidden;
  color: rgba(0, 0, 0, 0);
}

.input:focus {
  outline: none;
}

.input-label {
  position: absolute;
  top: 1rem;
  left: 0.5rem;
  transition: all 0.25s;
}

.input:not(:placeholder-shown) + .input-label,
.input:focus + .input-label {
  font-size: 0.875rem;
  top: 0.25rem;
  transition: all 0.25s;
}

.input-container--invalid {
  margin-bottom: 0.5rem;
}

.input-container--invalid .input {
  border: 1px solid #ff1f1f;
  border-radius: 7px;
}

.input-container--invalid .input-label {
  color: #b80b0b;
}

.input-message-error {
  display: none;
}

.input-container--invalid .input-message-error {
  color: #ff1f1f;
  display: block;
  margin-top: 0.5rem;
  padding-left: 0.5rem;
}

.textarea {
  box-sizing: border-box;
  border: 1px solid #FFFFFF;
  padding: 0.5rem;
  border-radius: 7px;
  width: 100%;
  min-height: 3rem;
}

.textarea-container {
  position: relative;
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-weight: 22px;
}

.textarea::placeholder {
  visibility: hidden;
}

.textarea:focus {
  outline: none;
}

.textarea-label {
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
  font-size: 22px;
  transition: all 0.25s;
}

.textarea:not(:placeholder-shown) + .textarea-label,
.textarea:focus + .textarea-label {
  font-size: 0.875rem;
  top: -1.25rem;
  transition: all 0.25s;
}

.textarea-container--invalid {
  margin-bottom: 0.5rem;
}

.textarea-container--invalid .textarea {
  border: 1px solid #ff1f1f;
}

.textarea-container--invalid .textarea-label {
  color: #ff1f1f;
}

.textarea-mensagem-erro {
  display: none;
}

.textarea-container--invalid .textarea-mensagem-erro {
  display: block;
  color: #464646;
  margin-top: 0.5rem;
  padding-left: 0.5rem;
}

.modal {
  background-color: #FFFFFF;
  border-radius: 10px;
  padding: 1.75rem;
  box-sizing: border-box;
  max-width: 22.5rem;
  position: relative;
}

.modal-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  transition: 0.25s;
}

.modal__title {
  font-size: 22px;
  color: #ff1f1f;
  margin-bottom: 1rem;
}

.modal__button {
  font-size: 19px;
}

.modal__texto {
  margin-bottom: 3.875rem;
}

.modal__button--confirm {
  color: #ff1f1f;
  margin-right: 2.25rem;
}

.modal__button-container {
  display: flex;
}

.modal__close {
  position: absolute;
  display: block;
  height: 1rem;
  width: 1rem;
  top: 1.75rem;
  right: 1.75rem;
}

.modal--closed {
  left: -100vw;
  transition: 0.25s;
}

