}

@media (min-width: 480px) {
  .logo {
    width: 17rem;
  }
  .boton {
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .barra {
    padding: 10px;
    margin: 0;
  }
  .header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
  }
  .contenido-header {
