.footer {
  background: linear-gradient(to right, #050a25 30%, #464646);
  text-align: center;
  padding: 1rem 0;
  margin: 0 auto;
  width: 100%;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
@media (min-width: 480px) {
  .footer .navegacion {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
  .footer .navegacion a {
    margin-right: 2rem;
  }
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 900px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin: 3rem 0 2rem 0;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 900px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
}
@media (min-width: 900px) {
  .barra {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.1s linear, height 0.3s linear;
}
@media (min-width: 900px) {
  .navegacion {
    opacity: 1;
    visibility: visible;
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 900px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}
@media (min-width: 900px) {
  .mostrar {
    flex-direction: row;
  }
}

@media (min-width: 900px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

